import * as React from 'react';
import { Controller, Node } from '@ali/4ever-cangjie';
import { Button, DeleteBetaNormal } from '@ali/we-design';
import { removeOnlineVideo } from '../actions';
import {
  InlineToolbar,
} from '../../../index';

interface IProps {
  controller: Controller;
  node: Node;
  locale: any;
  triggerNode: HTMLDivElement | null;
}

/* eslint-disable */
const OnlineVideoInlineToolbar = (props: IProps) => {
  const { locale = {}, triggerNode } = props;

  const onDeleteOnlineVideo = (
    e: React.MouseEvent<HTMLSpanElement> | React.TouchEvent<HTMLSpanElement>,
  ) => {
    e.preventDefault();
    e.stopPropagation();
    const { controller, node } = props;
    controller.run('onAction', removeOnlineVideo(node));
  };

  const DelOnlineVideoButton = () => {
    return <Button 
      className='onlinevideo-toolbar-delete'
      onClick={onDeleteOnlineVideo} 
      title={locale.delete} 
      bordered={false} 
      icon={<DeleteBetaNormal />} />;
  };

  const customToolButtons = {
    'onlineVideo.del': DelOnlineVideoButton,
  };

  const onlineVideoLayout = [{
      toolButtons: [{
        name: 'onlineVideo.del',
      }],
  }];

  return (
    <InlineToolbar
      visible
      triggerNode={triggerNode || undefined}
      customToolButtons={customToolButtons}
      inlineLayout={onlineVideoLayout}
    />
  );
};

export default React.memo(OnlineVideoInlineToolbar);
